<template>
  <div id="myChart" ref="chart" style="width:100%;height:100%" v-resize="redraw"></div>
</template>

<script>
export default {
  name: 'hello',
  data () {
    return {
      myChart: {},
      data: {},
      xAxisData: [],
      area: [],
      redlineSum: []
    }
  },
  props: {
    bar1Data: {}
  },
  watch: {
    bar1Data (val) {
      let Arr1 = []
      for (let item of val) {
        if (item.name) {
          Arr1.push(item.name)
        } else if (item.NAME) {
          Arr1.push(item.NAME)
        }
      }
      this.xAxisData = Arr1

      let Arr2 = []
      for (let item of val) {
        if (item.area === '' || item.area === null) {
          item.area = 0
        } else {
          item.area = item.area.toFixed(2)
        }
        Arr2.push(item.area)
      }
      this.area = Arr2

      let Arr3 = []
      for (let item of val) {
        Arr3.push(item.redlineSum)
      }
      this.redlineSum = Arr3

      this.drawLine()
    }
  },
  mounted () {
  },
  methods: {
    drawLine () {
      // domecharts
      this.myChart = this.$echarts.init(this.$refs.chart)
      // 
      this.myChart.setOption({
        title: {
          text: '',
          left: 35,
          top: 10
        },
        grid: {
          left: '5%',
          right: '5%',
          bottom: '7%',
          top: '80px',
          containLabel: true
        },
        legend: {
          data: ['', ''],
          top: 30,
          right: 60
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            crossStyle: {
              color: '#999'
            }
          },
          formatter: function (params) {
            return params[0].name + ':' + params[0].value + '<br>' + params[1].name + ':' + params[1].value + 'km'
          }
        },
        xAxis: {
          data: this.xAxisData
        },
        yAxis: [
          {
            type: 'value',
            name: '/',
            minInterval: 1,
            min: 0,
            axisLabel: {
              formatter: '{value}'
            }
          },
          {
            type: 'value',
            name: '/km',
            min: 0,
            axisLabel: {
              formatter: '{value}'
            }
          }
        ],
        series: [
          {
            name: '',
            type: 'bar',
            z: 3,
            itemStyle: {
              normal: {
                color: '#1ba9f5'
              }
            },
            data: this.redlineSum,
            barCategoryGap: '35%'
          },
          {
            name: '',
            type: 'bar',
            z: 3,
            itemStyle: {
              normal: {
                color: '#19ce9b'
              }
            },
            yAxisIndex: 1,
            data: this.area,
            barCategoryGap: '35%'
          }
        ]
      })
      // 
      window.addEventListener('resize', () => this.myChart.resize(), false)
    },
    redraw () {
      if (this.myChart.id !== undefined) {
        this.myChart.resize()
      }
    }
  }
}
</script>

<style lang="scss" rel="stylesheet/scss" scoped>

</style>
