<template>
  <div id="zdyQuery3" v-on:keyup.122 = "fullClose" v-on:keyup.27 = "fullClose">
    <div id="viewDiv1"></div>
    <div id="tools" ref="tools">
      <button @click.stop="choiceStateToggle" :class="{ active : choiceStatus }" title="">
        <i class="iconfont">&#xe6ed;</i>
      </button>
      <button @click.stop="addStateToggle" :class="{ active : addStatus }" title="">
        <i class="iconfont">&#xe66a;</i>
      </button>
    </div>
    <div id="datasLoist" ref="datasLoist" v-show="data1.length">
      <Table ref="table" border stripe :columns="columns1" :data="data1"  size="small" height="280"></Table>
    </div>
  </div>
</template>

<script>
import arcgisPackage from '../../utils/arcgisTools'
import config from '../../setting/mapConfig'
import choice from './choice'
import add from './add'
import { tdtlayer, tdtyNoteslayer, tdtclayer, tdtcNoteslayer, redLine, shengjie, shijie, xianjie, markerLayer, bspLayer, guaidianlayer } from '@/utils/layers'
import Basemapthumbnail from '../../assets/map_images/Basemapthumbnail.png'
import Basemapthumbnail1 from '../../assets/map_images/Basemapthumbnail1.png'
import collSign3 from '../../assets/boundaryMarker_images/coll_bm3.png'
import collBm3 from '../../assets/boundaryMarker_images/coll_sign3.png'
import { staticPoint } from '../../api/dataAnalysis'
const markerSymbol = {
  type: 'picture-marker', // autocasts as new SimpleMarkerSymbol()
  url: collSign3,
  height: 16,
  width: 16,
  outline: { // autocasts as new SimpleLineSymbol()
    color: [255, 255, 255],
    width: 1
  }
}
const bmSymbol = {
  type: 'picture-marker', // autocasts as new SimpleMarkerSymbol()
  url: collBm3,
  height: 16,
  width: 16,
  outline: { // autocasts as new SimpleLineSymbol()
    color: [255, 255, 255],
    width: 1
  }
}
export default {
  name: 'OnePiece',
  mixins: [ choice, add ],
  data () {
    return {
      map: null, // 
      view: null, // 
      type: '',
      columns1: [
        {
          title: '',
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('a', {
                style: {
                  color: '#2d8cf0',
                  textDecoration: 'underline'
                },
                on: {
                  click: () => {
                    this.gotopoint(params.row.X, params.row.Y)
                  }
                }
              }, params.row.)
            ])
          }
        }
      ],
      data1: []
    }
  },
  props: ['boxstate', 'obj'],
  methods: {
    /**
     * @Description: 
     * @author 
     * @date 2019/4/16
    */
    async createMap () {
      // arcgis
      let MapView = await arcgisPackage.MapView
      let Map = await arcgisPackage.Map
      let Basemap = await arcgisPackage.Basemap
      let BasemapToggle = await arcgisPackage.BasemapToggle
      let Fullscreen = await arcgisPackage.Fullscreen
      let GraphicsLayer = await arcgisPackage.GraphicsLayer

      // 
      let tdtylayer = await tdtlayer().then(res => { return res })
      // 
      let tdtylayer1 = await tdtclayer().then(res => { return res })
      // 
      let tdtylayer2 = await tdtcNoteslayer().then(res => { return res })
      // 
      let tdtylayer3 = await tdtyNoteslayer().then(res => { return res })

      let customBasemap = new Basemap({
        baseLayers: [tdtylayer1, tdtylayer2],
        title: '',
        id: 'myBasemap',
        thumbnailUrl: Basemapthumbnail
      })
      let customBasemap1 = new Basemap({
        baseLayers: [tdtylayer, tdtylayer3],
        title: '',
        id: 'myBasemap1',
        thumbnailUrl: Basemapthumbnail1
      })

      this.map = new Map({
        basemap: customBasemap
      })
      this.view = new MapView({
        map: this.map,
        container: 'viewDiv1',
        center: config.centerPoint,
        zoom: 5
      })
      // 
      this.view.ui.add(this.$refs.tools, 'bottom-right')
      this.view.ui.add(this.$refs.datasLoist, 'bottom-left')
      // 
      this.fullscreen = new Fullscreen({
        view: this.view
      })
      this.view.ui.add(this.fullscreen, 'top-left')
      // 
      let basemapToggle = new BasemapToggle({
        view: this.view,
        nextBasemap: customBasemap1
      })
      this.view.ui.padding = { top: 20, left: 20, right: 55, bottom: 20 }
      this.view.ui.add(basemapToggle, 'top-right')
      // 
      let graphicsLayer = new GraphicsLayer({
        id: 'poi',
        title: ' ',
        elevationInfo: 'on-the-ground'
      })
      this.map.add(graphicsLayer)
      let layertemp = await bspLayer('poi12')
      this.map.layers.add(layertemp)
    },
    async gotopoint (x, y) {
      let arr = []
      for (let item of this.map.findLayerById('poi').graphics.items) {
        item.symbol = {
          type: 'simple-marker',
          color: [255, 184, 0, 0.001],
          size: '10px',
          outline: {
            color: [255, 184, 0, 0.001],
            width: 3
          }
        }
        if (item.attributes.X === x && item.attributes.Y) {
          arr.push(item)
        }
      }
      this.view.goTo(arr[0])
      arr[0].symbol = {
        type: 'simple-marker',
        color: [255, 184, 0, 0.001],
        size: '10px',
        outline: {
          color: [255, 184, 0, 0.8],
          width: 3
        }
      }
      this.view.zoom = 14
    }
  },
  async mounted () {
    // 
    await this.createMap()
  }
}
</script>

<style lang="scss" scoped>
#zdyQuery3{
  height: 100%;
  display: flex;
  position: relative;
}
#layersControl{
  .layersControl_box{
    padding:3px;
    background: #fff;
    border: 1px solid #d1d1d1;
    .fraggableBox{
      padding: 0;
      .fraggableLi{
        min-width: 100px;
        padding: 5px 8px;
        border-top: 1px solid #d1d1d1;
      }
    }
  }
}
#viewDiv1{
  height: 100%;
  flex-grow: 2;
}
#tools{
  background: #fff;
  margin-right: 5px;
  padding: 5px;
  float: right;
  button {
    width: 30px;
    height: 30px;
    border: 0;
    background: #fff;
    cursor: pointer;
    outline: none;
    i {
      font-size: 16px;
    }
  }
  .active {
    background-color: #4C4C4C;
    color: #fff;
  }
}
#datasLoist{
  background: #fff;
  width: 180px;
  height: 280px;
}
#maptab{
  width: 300px;
  .firstmenu{
    >li{
      margin-bottom: 6px;
      text-align: left;
      >ul{
        >li{
          padding-left: 20px;
        }
      }
      >p{
        line-height: 40px;
        text-align: left;
        font-size: 16px;
        background-color: rgb(42,177,245);
        color: white;
        padding-left: 10px;
        border-radius: 4px;
        cursor: pointer;
        i{
          font-size: 16px;
          line-height: 40px;
          margin-right: 10px;
        }
      }
      .secondmenu{
        label{
          line-height: 30px;
          font-size: 14px;
        }
      }
    }
  }
}
#basemapToggle{
  border: 5px #000 solid;
}
</style>
<style scoped>
#maptab >>> .ivu-tabs-content{
  height: calc(100% - 40px);
  margin-top: -16px;
}
#maptab >>> .ivu-tabs-content > .ivu-tabs-tabpane{
  padding: 16px;
  border: 1px solid #dcdee2;
  border-radius: 0 0 4px 4px;
  border-top: none;
}
#maptab >>> .ivu-tabs-nav{
  display: flex;
  float: none;
}
#maptab >>> .ivu-tabs-nav .ivu-tabs-tab{
  flex-grow: 2;
  margin-right: 0;
}
  .sliderBox >>> .ivu-slider-wrap{
    margin: 0 0 3px
  }
</style>
<style lang="scss">
  #zdyQuery3{
    .esri-icon-polyline, .esri-icon-map-pin{display: none}
  }
</style>
