<template>
  <div id="myChart" ref="chart" style="width:100%;height:100%"  v-resize="redraw"></div>
</template>

<script>
export default {
  name: 'hello',
  data () {
    return {
      myChart: {},
      data: {},
      legendData: [],
      pidData: [],
      seriesData: []
    }
  },
  props: {
    bar1Data: {}
  },
  watch: {
    bar1Data (val) {
      let Arr1 = []
      for (let item of val) {
        Arr1.push(item.name)
      }
      this.legendData = Arr1
      let Arr2 = []
      for (let item of val) {
        Arr2.push(item.area * 1)
      }
      this.pidData = Arr2
      let Arr3 = []
      for (let i in this.legendData) {
        let obj = {
          name: this.legendData[i],
          value: this.pidData[i]
        }
        Arr3.push(obj)
      }
      this.seriesData = Arr3
      this.drawLine()
    }
  },
  methods: {
    drawLine () {
      // domecharts
      this.myChart = this.$echarts.init(this.$refs.chart)
      // 
      this.myChart.setOption({
        title: {
          text: '',
          subtext: '',
          top: 20,
          left: 'center'
        },
        tooltip: {
          trigger: 'item',
          formatter: '{b} : {c}m ({d}%)'
        },
        series: [
          {
            type: 'pie',
            radius: '45%',
            center: ['50%', '55%'],
            data: this.seriesData,
            itemStyle: {
              emphasis: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }
        ]
      })
      // 
      window.addEventListener('resize', () => this.myChart.resize(), false)
    },
    redraw () {
      if (this.myChart.id !== undefined) {
        this.myChart.resize()
      }
    }
  }
}
</script>

<style lang="scss" rel="stylesheet/scss" scoped>

