<template>
<div id="mapbox" v-on:keyup.122 = "fullClose" v-on:keyup.27 = "fullClose">
  <div v-show="boxstate===2" style="padding: 10px" id="maptab">
    <Tabs type="card" style="height: 100%" @on-click="tabChange">
      <TabPane label="">
        <ul class="firstmenu">
          <li>
            <p @click="activeSecondMenu=1"><Icon style="float: right" type="ios-arrow-down" /></p>
            <ul class="secondmenu" v-show="activeSecondMenu===1">
              <li><Checkbox v-model="layer.layer1" @on-change="addLayer(1, $event)"></Checkbox></li>
              <li><Checkbox v-model="layer.layer2" @on-change="addLayer(2, $event)"></Checkbox></li>
              <li><Checkbox v-model="layer.layer3" @on-change="addLayer(3, $event)"></Checkbox></li>
              <li><Checkbox v-model="layer.layer4" @on-change="addLayer(4, $event)"></Checkbox></li>
            </ul>
          </li>
        </ul>
        <ul class="firstmenu">
          <li>
            <p @click="activeSecondMenu=2"><Icon style="float: right" type="ios-arrow-down" /></p>
            <ul class="secondmenu" v-show="activeSecondMenu===2">
              <li><Checkbox v-model="layer.layer1" @on-change="addLayer(1, $event)"></Checkbox></li>
              <li v-for="item in dituList" v-bind:key="item.id">
                <Checkbox  @on-change="addLayer1(item, $event)">{{item.name}}</Checkbox>
              </li>
            </ul>
          </li>
        </ul>
      </TabPane>
      <TabPane label="">
        <ul class="firstmenu">
          <li><Button :type="zdyQuery === 1 ? 'primary' : 'default'" @click="zdyQuery = 1" style="width: 100%"></Button></li>
          <li><Button :type="zdyQuery === 2 ? 'primary' : 'default'" @click="zdyQuery = 2" style="width: 100%"></Button></li>
          <li><Button :type="zdyQuery === 3 ? 'primary' : 'default'" @click="zdyQuery = 3" style="width: 100%"></Button></li>
        </ul>
      </TabPane>
    </Tabs>
  </div>
  <div id="layersControl">
    <div class="layersControl_box" v-if="layerList.length !== 0">
      <p style="padding:5px;text-align: center;cursor: pointer;" @click="layerCon = !layerCon;$emit('layerConClick')">
        
        <Icon :type="layerCon ? 'md-arrow-dropdown' : 'md-arrow-dropright'" />
      </p>
      <draggable v-model="layerList" class="fraggableBox" v-if="layerCon">
        <div v-for="(layer,index) in layerList" class="fraggableLi" :key="index">
          <p style="text-align: left;display:flex;align-items: center;">
            <span>{{layer.name}}</span>
            </p>
          <div class="sliderBox">
            <Slider v-model="layer.opacity" :min="0" :max="1" :step="0.01"></Slider>
          </div>
        </div>
      </draggable>
    </div>
  </div>
  <div id="viewDiv" v-show="actTabName === 0"></div><!--  -->
  <div id="zdyQuery" v-show="actTabName === 1">
    <div v-if="zdyQuery === 1">
      <zdyQuery1 ref="zdymap1"></zdyQuery1>
    </div>
    <div v-if="zdyQuery === 2">
      <zdyQuery2 ref="zdymap2"></zdyQuery2>
    </div>
    <div v-if="zdyQuery === 3">
      <zdyQuery3 ref="zdymap3"></zdyQuery3>
    </div>
  </div>
  <ButtonGroup id="toolbar" vertical style="display: flex;flex-direction: column">
    <Tooltip content="" placement="right">
      <Button :type="type=='distance'?'primary':'default'" @click="clickToolbar('distance')" style="padding:5px 5px 0px">
        <img src="@/assets/map_images/length.png" alt="" style="width: 20px;height: 20px;">
      </Button>
    </Tooltip>
    <Tooltip content="" placement="right">
      <Button :type="type=='area'?'primary':'default'" @click="clickToolbar('area')" style="padding:5px 5px 0px">
        <img src="@/assets/map_images/area.png" alt="" style="width: 20px;height: 20px;">
      </Button>
    </Tooltip>
  </ButtonGroup>
</div>
</template>

<script>
import arcgisPackage from '../../utils/arcgisTools'
import config from '../../setting/mapConfig'
import { tdtlayer, tdtyNoteslayer, tdtclayer, tdtcNoteslayer, redLine, shengjie, shijie, xianjie, markerLayer, bspLayer, guaidianlayer } from '@/utils/layers'
import draggable from 'vuedraggable'
import zdyQuery1 from './zdyQuery1'
import zdyQuery2 from './zdyQuery2'
import zdyQuery3 from './zdyQuery3'
import { getAllPreMarkerList, getAllPreBoard, getAllMarkerList, getAllBoard, getAllPoint, dituList } from '@/api/acquisitionTable'
import guaiPoint3 from '../../assets/boundaryMarker_images/guai_point3.png'
import presetBm3 from '../../assets/boundaryMarker_images/preset_bm3.png'
import presetSign3 from '../../assets/boundaryMarker_images/preset_sign3.png'
import collBm3 from '../../assets/boundaryMarker_images/coll_bm3.png'
import collSign3 from '../../assets/boundaryMarker_images/coll_sign3.png'
import Basemapthumbnail from '../../assets/map_images/Basemapthumbnail.png'
import Basemapthumbnail1 from '../../assets/map_images/Basemapthumbnail1.png'

export default {
  name: 'OnePiece',
  components: {
    draggable,
    zdyQuery1,
    zdyQuery2,
    zdyQuery3
  },
  data () {
    return {
      map: null, // 
      view: null, // 
      layer: {
        layer1: false, // 
        layer2: false, // 
        layer3: false, // 
        layer4: false // 
      },
      activeSecondMenu: 0, // 
      layerList: [],
      layerCon: false, // 
      AllPoint: [], // 
      ActulTabletData: [], // 
      ActulSignData: [], // 
      PreTabletData: [], // 
      PreSignData: [], // 
      type: '',
      defaultLayerArr: [4, 5, 6, 7, 8, 9, 1, 2, 3],
      fullscreen: null,
      print: undefined,
      dituList: [], // 
      zdyQuery: 1, // 
      actTabName: 0 // 
    }
  },
  props: ['boxstate'],
  watch: {
    layerList: {
      handler (val) {
        for (let i in val) {
          this.map.reorder(this.map.findLayerById(val[i].id), 0)
          this.map.findLayerById(val[i].id).opacity = val[i].opacity
        }
      },
      immediate: false,
      deep: true
    },
    boxstate (val) {
      if (val === 2) {
        this.addPrint()
      } else {
        this.removePrint()
      }
    }
  },
  methods: {
    getDituList () {
      dituList({
        data: {
        }
      }).then(res => {
        if (res.data.code === '0000') {
          this.dituList = res.data.data.rows
        } else {
          this.$Notice.error({ title: '', desc: res.data.msg })
        }
      })
    },
    /**
     * @Description: 
     * @author 
     * @date 2019/4/16
    */
    async createMap () {
      // arcgis
      let MapView = await arcgisPackage.MapView
      let Map = await arcgisPackage.Map
      let Basemap = await arcgisPackage.Basemap
      let BasemapToggle = await arcgisPackage.BasemapToggle
      let Fullscreen = await arcgisPackage.Fullscreen

      // 
      let tdtylayer = await tdtlayer().then(res => { return res })
      // 
      let tdtylayer1 = await tdtclayer().then(res => { return res })
      // 
      let tdtylayer2 = await tdtcNoteslayer().then(res => { return res })
      // 
      let tdtylayer3 = await tdtyNoteslayer().then(res => { return res })

      let customBasemap = new Basemap({
        baseLayers: [tdtylayer1, tdtylayer2],
        title: '',
        id: 'myBasemap',
        thumbnailUrl: Basemapthumbnail
      })
      let customBasemap1 = new Basemap({
        baseLayers: [tdtylayer, tdtylayer3],
        title: '',
        id: 'myBasemap1',
        thumbnailUrl: Basemapthumbnail1
      })

      this.map = new Map({
        basemap: customBasemap
      })
      this.view = new MapView({
        map: this.map,
        container: 'viewDiv',
        center: config.centerPoint,
        zoom: 5
      })
      // 
      this.view.ui.add(document.getElementById('toolbar'), 'top-left')
      // 
      this.fullscreen = new Fullscreen({
        view: this.view
      })
      this.view.ui.add(this.fullscreen, 'top-left')
      // 
      let basemapToggle = new BasemapToggle({
        view: this.view,
        nextBasemap: customBasemap1
      })
      this.view.ui.padding = { top: 20, left: 20, right: 55, bottom: 20 }
      this.view.ui.add(basemapToggle, 'top-right')
      this.view.ui.add(document.getElementById('layersControl'), 'top-left')
    },
    /**
     * @Description: 
     * @author 
     * @date 2019/5/15
    */
    async addLayer (index, flag) {
      if (flag) {
        if (this.map.findLayerById(index.toString()) === undefined) {
          let layertemp = {}
          switch (index) {
            // 
            case 1:
              layertemp = await guaidianlayer(index)
              break
            // 
            case 2:
              layertemp = await markerLayer(index)
              break
            case 3:
              layertemp = await bspLayer(index)
              break
            case 4:
              layertemp = await redLine(index)
              break
            default:
              layertemp = ''
              break
          }
          this.map.layers.add(layertemp)
          this.layerList.push({ name: layertemp.name, id: index.toString(), opacity: 1, legend: layertemp.legend })
        } else {
          this.map.findLayerById(index.toString()).visible = true
          this.layerList = this.layerList.filter(data => { return data.id !== index.toString() })
          this.layerList.push({ name: this.map.findLayerById(index.toString()).name, id: index.toString(), opacity: 1, legend: this.map.findLayerById(index.toString()).legend })
        }
      } else {
        this.layerList = this.layerList.filter(data => { return data.id !== index.toString() })
        this.map.findLayerById(index.toString()).visible = false
      }
    },
    addLayer1 (layer, flag) {
      if (flag) {
        let templayer = this.map.findLayerById('ditu' + layer.id)
        if (templayer) {
          this.map.remove(templayer)
        } else {
          switch (layer.type) {
            case 0:
              this.addTileLayer(layer)
              break
            case 1:
              this.addFeatureLayer(layer)
              break
            case 2:
              this.addTileRestLayer(layer)
              break
            case 3:
              this.addWMSLayer(layer)
              break
            case 4:
              this.addWMTSLayer(layer)
              break
            default:
              break
          }
        }
      } else {
        this.layerList = this.layerList.filter(data => { return data.id !== ('ditu' + layer.id) || data.id.indexOf('ditu') === -1 })
        this.map.remove(this.map.findLayerById('ditu' + layer.id))
      }
    },

    /*  */
    actAllLayer () {
      for (let i in this.defaultLayerArr) {
        this.addLayer(this.defaultLayerArr[i], true)
      }
      for (let i in this.layer) {
        this.layer[i] = true
      }
    },
    /*  */
    async addPrint () {
      let Print = await arcgisPackage.Print
      this.print = new Print({
        view: this.view,
        printServiceUrl:
        // '/arcgis/rest/services/Utilities/PrintingTools/GPServer'
          config.serverUrl + '/arcgis/rest/services/Utilities/PrintingTools/GPServer/Export%20Web%20Map%20Task'
      })
      this.view.ui.add(this.print, 'bottom-right')
    },
    removePrint () {
      if (this.print) {
        this.print.destroy()
      }
    },

    // REST
    async addFeatureLayer (layer) {
      let FeatureLayer = await arcgisPackage.FeatureLayer
      let featurelayer = new FeatureLayer({
        url: layer.url,
        outFields: ['*'],
        id: 'ditu' + layer.id
      })
      this.map.layers.add(featurelayer)
      this.layerList.push({ name: layer.name, id: 'ditu' + layer.id, opacity: 1, legend: featurelayer })
    },
    // REST
    async addTileLayer (layer) {
      let MapImageLayer = await arcgisPackage.MapImageLayer
      let tileLayer = new MapImageLayer({
        url: layer.url,
        id: 'ditu' + layer.id
      })
      this.map.layers.add(tileLayer)
      console.log(layer)
      this.layerList.push({ name: layer.name, id: 'ditu' + layer.id, opacity: 1, legend: tileLayer })
    },
    // REST
    async addTileRestLayer (layer) {
      let TileLayer = await arcgisPackage.TileLayer
      let tileLayer = new TileLayer({
        url: layer.url,
        id: 'ditu' + layer.id
      })
      this.map.layers.add(tileLayer)
      this.layerList.push({ name: layer.name, id: 'ditu' + layer.id, opacity: 1, legend: tileLayer })
    },
    // WMS
    async addWMSLayer (layer) {
      let WMSLayer = await arcgisPackage.WMSLayer
      let wmslayer = new WMSLayer({
        url: layer.url,
        id: 'ditu' + layer.id
      })
      this.map.layers.add(wmslayer)
      this.layerList.push({ name: layer.name, id: 'ditu' + layer.id, opacity: 1, legend: wmslayer })
    },
    // WMTS
    async addWMTSLayer (layer) {
      let WMTSLayer = await arcgisPackage.WMTSLayer
      let wmtslayer = new WMTSLayer({
        url: layer.url,
        id: 'ditu' + layer.id
      })
      this.map.layers.add(wmtslayer)
      this.layerList.push({ name: layer.name, id: 'ditu' + layer.id, opacity: 1, legend: wmtslayer })
    },
    /**
     * @Description:
     * @author 
     * @date 2019/4/11
     */
    async clickToolbar (type) {
      if (this.type === type) {
        type = null
      }
      this.type = type
      if (this.activeWidget) {
        this.view.ui.remove(this.activeWidget)
        this.activeWidget.destroy()
        this.activeWidget = null
      }
      switch (type) {
        case 'distance':
          let DistanceMeasurement2D = await arcgisPackage.DistanceMeasurement2D
          this.activeWidget = new DistanceMeasurement2D({
            view: this.view
          })
          this.activeWidget.viewModel.newMeasurement()
          this.view.ui.add(this.activeWidget, 'top-right')
          break
        case 'area':
          let AreaMeasurement2D = await arcgisPackage.AreaMeasurement2D
          this.activeWidget = new AreaMeasurement2D({
            view: this.view
          })
          this.activeWidget.viewModel.newMeasurement()
          this.view.ui.add(this.activeWidget, 'top-right')
          break
      }
    },
    tabChange (name) {
      console.log(name)
      this.actTabName = name
    }
  },
  created () {
    // this.getAllPoint()
    // this.getAllPreMarkerList()
    // this.getAllPreBoard()
    // this.getAllBoard()
    // this.getAllMarkerList()
    this.getDituList()
  },
  async mounted () {
    // 
    await this.createMap()
    this.view.on('resize', () => {
      if (this.fullscreen.viewModel.state === 'active') {
        this.actAllLayer()
      }
    })
  }
}
</script>

<style lang="scss" scoped>
#mapbox{
  height: 100%;
  display: flex;
  position: relative;
}
#layersControl{
  .layersControl_box{
    padding:3px;
    background: #fff;
    border: 1px solid #d1d1d1;
    .fraggableBox{
      padding: 0;
      .fraggableLi{
        min-width: 100px;
        padding: 5px 8px;
        border-top: 1px solid #d1d1d1;
      }
    }
  }
}
#viewDiv{
  height: 100%;
  flex-grow: 2;
}
#zdyQuery{
  height: 100%;
  flex-grow: 2;
  >div{
    height: 100%;
  }
}
#maptab{
  width: 300px;
  .firstmenu{
    >li{
      margin-bottom: 6px;
      text-align: left;
      >ul{
        >li{
          padding-left: 20px;
        }
      }
      >p{
        line-height: 40px;
        text-align: left;
        font-size: 16px;
        background-color: rgb(42,177,245);
        color: white;
        padding-left: 10px;
        border-radius: 4px;
        cursor: pointer;
        i{
          font-size: 16px;
          line-height: 40px;
          margin-right: 10px;
        }
      }
      .secondmenu{
        label{
          line-height: 30px;
          font-size: 14px;
        }
      }
    }
  }
}
#basemapToggle{
  border: 5px #000 solid;
}
</style>
<style scoped>
#maptab >>> .ivu-tabs-content{
  height: calc(100% - 40px);
  margin-top: -16px;
}
#maptab >>> .ivu-tabs-content > .ivu-tabs-tabpane{
  padding: 16px;
  border: 1px solid #dcdee2;
  border-radius: 0 0 4px 4px;
  border-top: none;
}
#maptab >>> .ivu-tabs-nav{
  display: flex;
  float: none;
}
#maptab >>> .ivu-tabs-nav .ivu-tabs-tab{
  flex-grow: 2;
  margin-right: 0;
}
  .sliderBox >>> .ivu-slider-wrap{
    margin: 0 0 3px
  }
</style>
