import config from './config'
import arcgisPackage from '@/utils/arcgisTools'
/*  */
export default {
  data () {
    return {
      addStatus: false
    }
  },
  methods: {
    /*  */
    closeOtherTools (toolNames) {
      this.cursorType = 'default'
      if (!toolNames) {
        toolNames = ''
      }
      /*  */
      if (toolNames.indexOf('choice') === -1) {
        if (this.choiceStatus) {
          this.choiceStatus = false
          this.delChoice()
        }
      }
      /*  */
      if (toolNames.indexOf('add') === -1) {
        if (this.addStatus) {
          this.delSketch()
        }
      }
    },
    /*  */
    async addStateToggle () {
      /*  */
      this.closeOtherTools('add')
      if (!this.addStatus) {
        this.map.findLayerById('poi').removeAll()
        this.addStatus = true
        this.addSketch()
      } else {
        this.delSketch()
      }
    },
    /*  */
    async addSketch () {
      let Sketch = await arcgisPackage.Sketch
      this.sketch = new Sketch({
        layer: this.map.findLayerById('poi'),
        view: this.view
      })
      this.view.ui.add(this.sketch, 'bottom-right')
      this.sketch.on('update', (event) => {
        if (event.state === 'complete') {
          this.map.findLayerById('sLayer').remove(event.graphic)
        }
      })
    },
    /*  */
    async delSketch () {
      console.log(111111)
      let geometryEngine = await arcgisPackage.geometryEngine
      // 
      let arr = this.map.findLayerById('poi').graphics.items[0]
      // 
      let gras = []
      this.map.findLayerById('poi12').queryFeatures().then((res) => {
        for (let item of res.features) {
          let intersect = geometryEngine.intersects(arr.geometry, item.geometry)
          if (intersect) {
            gras.push(item)
          }
        }
        this.originalSelectedGraphics = gras
        this.map.findLayerById('poi').removeAll()
        this.map.findLayerById('poi').addMany(gras)
      }).then(res => {
        console.log(this.map.findLayerById('poi').graphics.items)
        let arr = []
        for (let item of this.map.findLayerById('poi').graphics.items) {
          arr.push(item.attributes)
        }
        this.data1 = arr
      })
      if (!this.sketch) {
        this.view.ui.remove(this.sketch)
        this.sketch = undefined
      }
    }
  }
}
